/* ==========================================================================
   Blog Single style - begin
   ========================================================================== */

.edgtf-blog-holder.edgtf-blog-single {
	
	article {
		margin: 0 0 51px;
		
		.edgtf-post-heading {
			margin-bottom: 36px;
		}
		
		.edgtf-post-text {
			padding: 0 0 21px;
		}

		.edgtf-post-info-top{
			margin-bottom: 7px;
		}

		&.format-link,
		&.format-quote {
			padding: 0 !important;

			.edgtf-post-text {
				padding: 26px 43px;
			}

			.edgtf-post-info-top{
				margin-bottom: 2px;
			}
		}

		.edgtf-post-info-bottom{
			border-top: 1px solid #e0e4f2;
			margin-top: 58px;
			padding-top: 20px;
			
			h4{
				margin: 0;
				display: inline-block;
				padding: 0 18px;

				&:before{
					content: '';
					height: 100%;
					position: absolute;
					left: 0;
					background-color: rgba(32,32,32,.3);
					width: 1px;
				}
			}
		}
	}
}
/* ==========================================================================
   Blog Single style - end
   ========================================================================== */