/* ==========================================================================
   Blog List shortcode - Alternating type style - begin
   ========================================================================== */

    .edgtf-bl-alternating{

        .edgtf-bli-content{
            position: relative;
            width: 100%;
            left: 0;
            height: 100%;
            background-image: url(../../../../../assets/img/default_pattern.png);
            padding: 10px;
            margin: 3% 0;

            .edgtf-bli-content-inner{
                background-color: #fff;
                height: 100%;
            }

            .edgtf-bli-content-holder{
                width: 50%;
                padding: 3% 4%;
                box-sizing: border-box;
                left: 50%;
                position: relative;
            }

            .edgtf-post-read-more-button{
                margin-top: 7px;

                polyline{
                    stroke: currentColor;
                }
            }
        }

        .edgtf-post-image{
            width: 50%;
            padding: 0 4%;
            box-sizing: border-box;
            margin: 0;
            z-index: 100;
            position: absolute;
            height: 100%;

            a{
                height: 100%;

                img{
                    height: 100%;
                }

                &:after{
                    width: 84%;
                    left: 50%;
                    @include edgtfTransform(translateX(-50%));
                }
            }
        }


        
        .edgtf-bl-item{
            &:nth-child(even){
                .edgtf-post-image{
                    right: 0;
                }
                .edgtf-bli-content{
                    .edgtf-bli-content-inner{
                        .edgtf-bli-content-holder{
                            left: 0;
                            text-align: right;
                        }
                    }
                }

                .edgtf-btn.edgtf-btn-simple.edgtf-btn-text-on-hover{
					.edgtf-btn-text{
                		@include edgtfTransform(translate3d(100%,0,0));
					}

                	.edgtf-btn-arrow{
                		left: 0;
                		right: auto;
                		@include edgtfTransform(scaleX(-1));
                	}

                	&:hover{
                		.edgtf-btn-text{
                			@include edgtfTransform(translate3d(0,0,0));
                		}
                	}
                }
            }
        }

        &.light-skin {
            .edgtf-bli-content {
                .edgtf-bli-content-inner {
                    background-color: #111;
                }
            }
        }

        &.edgtf-show-info-on-appear {
            .edgtf-bl-item {
                .edgtf-bli-content-holder,
                .edgtf-post-image {
                    overflow: hidden;
                }

                &:nth-child(odd) {
                    .edgtf-post-image img {
                        -webkit-clip-path: inset(0 0 0 100%);
                        clip-path: inset(0 0 0 100%);
                    }

                    .edgtf-bli-content-holder-inner {
                        @include edgtfTransform(translate3d(-110%,0,0));
                    }
                }

                &:nth-child(even) {
                    .edgtf-post-image img {
                        -webkit-clip-path: inset(0 100% 0 0);
                        clip-path: inset(0 100% 0 0);
                    }

                    .edgtf-bli-content-holder-inner {
                        @include edgtfTransform(translate3d(110%,0,0));
                    }
                }

                .no-touch & {
                    .edgtf-post-image img {
                        @include edgtfTransition(.7s cubic-bezier(0.6, 0.3, 0.11, 0.86));
                    }

                    .edgtf-bli-content-holder-inner {
                        @include edgtfTransitionTransform(.35s cubic-bezier(0.71, 0.19, 0, 0.76) .35s);
                    }
                }

                .touch &,
                &.edgtf-appear {
                    .edgtf-post-image img {
                        -webkit-clip-path: inset(0 0 0 0);
                        clip-path: inset(0 0 0 0);
                    }

                    .edgtf-bli-content-holder-inner {
                        @include edgtfTransform(translate3d(0,0,0));
                    }
                }

                //ie fallback
                .no-touch .edgtf-ms-explorer & {
                    .edgtf-post-image img {
                        opacity: 0;
                    }
                }

                .touch .edgtf-ms-explorer &,
                .edgtf-ms-explorer &.edgtf-appear {
                    .edgtf-post-image img {
                        opacity: 1;
                    }
                }
            }
        }
    }

/* ==========================================================================
   Blog List shortcode - Alternating type style - end
   ========================================================================== */