/* ==========================================================================
   Blog List - Masonry style - begin
   ========================================================================== */

.edgtf-blog-holder {
	
    &.edgtf-blog-masonry {

        article {
            margin: 0 0 20px;
            padding: 0 0 30px;

            &.edgtf-post-has-media {
                
                .edgtf-post-heading {
                    margin-bottom: 30px;
                }
            }
        }
    }
}
