/* ==========================================================================
   Blog List shortcode - Simple type style - begin
   ========================================================================== */

.edgtf-blog-list-holder {
    
    &.edgtf-bl-simple {
        
        .edgtf-post-image {
            display: table-cell;
            vertical-align: middle;
            width: 79px;
        }
        
        .edgtf-bli-content {
            display: table-cell;
            vertical-align: top;
            width: auto;
            padding-left: 20px;

            .edgtf-post-title{
                a{
                    font-style: normal;
                }
            }
            
            .edgtf-post-info-date {
                display: block;
                margin: 7px 0 0;

                
                a {
                    color: rgba($default-heading-color, .6);
                    font-size: 16px;
                    font-weight: 300;
                    text-transform: none;
                    letter-spacing: 0.02em;
                    line-height: 1.63em;
                    font-style: italic;

                    &:hover {
                        color: $default-heading-color;
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   Blog List shortcode - Simple type style - end
   ========================================================================== */