/* ==========================================================================
   Blog List shortcode global style - begin
   ========================================================================== */

.edgtf-blog-list-holder {
	@include edgtfRelativeHolderLayout();
	clear: both;
	
	.edgtf-blog-list {
		@include edgtfRelativeHolderLayout();
		list-style: none;
		margin: 0;
		padding: 0;
	}
	
	.edgtf-bl-item {
		@include edgtfRelativeHolderLayout();
		padding: 0;
		margin: 0;
		box-sizing: border-box;
	}
	
	.edgtf-bli-inner {
		@include edgtfRelativeHolderLayout();
	}
	
	.edgtf-bli-content {
		@include edgtfRelativeHolderLayout();
		box-sizing: border-box;
	}
	
	.edgtf-post-image {
		@include edgtfRelativeHolderLayout();
		margin: 0 0 30px;
		
		a {
			display: block;
		}
		
		img {
			display: block;
		}
	}
	
	.edgtf-bli-info {
		@include edgtfRelativeHolderLayout();
		margin: 0 0 8px;
		clear: both;
		
		> div {
			position: relative;
			display: inline-block;
			vertical-align: top;
			font-size: 16px;
			padding: 0 9px 0 0;
			margin-right: 5px;
			color: rgba($default-heading-color, .6);
			font-family: $default-heading-font;
			font-weight: 300;
			text-transform: none;
			letter-spacing: .02em;
			line-height: 1.63em;
			font-style: italic;

			&:after{
				content: '|';
				position: absolute;
				top: 0;
				right: 0;
				font-style: normal;
			}
			
			&:last-child {
				padding: 0;
				margin: 0;

				&:after {
					content: '';
				}
			}
			
			a {
				color: rgba($default-heading-color, .6);

				&:hover {
					color: $first-main-color;
				}
			}
		}
	}
	
	.edgtf-post-title {
		margin: 0 0 4px;
	}
	
	.edgtf-post-excerpt {
		@include edgtfRelativeHolderLayout();
		margin: 10px 0 0;
		
		p {
			margin: 0;
		}
		
		&:after {
			content: '...';
		}
	}
	
	.edgtf-post-read-more-button {
		@include edgtfRelativeHolderLayout();
		margin: 17px 0 0;

		polyline{
			stroke: currentColor;
		}
	}

	&.light-skin{
		a,
		p,
		div,
		.edgtf-bli-info > div a{
			color: #fff;
		}
	}
}
/* ==========================================================================
   Blog List shortcode global style - end
   ========================================================================== */