/* ==========================================================================
   Blog Common style - begin
   ========================================================================== */

.edgtf-blog-holder {
    @include edgtfRelativeHolderLayout();
    margin: 0 0 40px;
    clear: both;

    &.edgtf-blog-single{
        article {
            &.format-link,
            &.format-quote {
                background-image: none;

                .edgtf-post-content {
                    background-image: url(../../../../../assets/img/default_pattern.png);
                    padding: 10px;
                    margin-bottom: 25px;
                    box-sizing: border-box;
                }
            }
        }
    }

    article {
        @include edgtfRelativeHolderLayout();
        box-sizing: border-box;

        &.sticky {

            .edgtf-post-title {
                &:before {
                    content: '\e031';
                    font-family: 'ElegantIcons';
                    margin-right: 10px;
                }
            }
        }

        .edgtf-post-content {
            @include edgtfRelativeHolderLayout();
        }

        .edgtf-post-image {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            max-width: 100%;

            img {
                display: block;
            }
        }
    
        .edgtf-post-image-background {
            @include edgtfRelativeHolderLayout();
            height: 100%;
            background-position: center;
            background-size: cover;
        
            a {
                @include edgtfAbsoluteHolderLayout();
            }
        }
    
        .edgtf-post-image-icon {
            position: absolute;
            top: 20px;
            right: 20px;
            font-size: 30px;
            line-height: 1;
            color: #fff;
        }

        .edgtf-post-text {
            @include edgtfRelativeHolderLayout();
            box-sizing: border-box;
        }
    
        .edgtf-post-title {
            margin: 0 0 4px;
        }
    
        .edgtf-post-excerpt-holder {
            @include edgtfRelativeHolderLayout();
            margin: 15px 0 35px;
        
            p {
                margin: 0;
            }
        }
    
        .edgtf-post-excerpt {
        
            &:after {
                content: '...';
            }
        }
    
        .edgtf-more-link-container {
            @include edgtfRelativeHolderLayout();
            margin: 15px 0 0;
        }

        .edgtf-post-info {
            @include edgtfRelativeHolderLayout();

            > * {
                position: relative;
                display: inline-block;
                vertical-align: top;
                color: currentColor;

                a {
                    color: inherit;
                }
            }
        }
    
        .edgtf-post-info-top {
            margin-bottom: 2px;
        
            > div {
                position: relative;
                display: inline-block;
                vertical-align: top;
                padding: 0 9px 0 0;
                color: rgba($default-heading-color, .6);
                font-family: $default-heading-font;
                font-size: 16px;
                font-weight: 300;
                text-transform: none;
                letter-spacing: 0.02em;
                line-height: 1.63em;
                font-style: italic;
    
                &:after {
                    content: '|';
                    position: absolute;
                    top: 0;
                    right: 0;
                    font-style: normal;
                }
    
                &:last-child {
                    padding: 0;
        
                    &:after {
                        content: '';
                    }
                }
    
                a {
                    color: inherit;
        
                    &:hover {
                        color: $first-main-color;
                    }
                }
            }
        }
    
        .edgtf-post-info-bottom {
        
            .edgtf-post-info-bottom-left,
            .edgtf-post-info-bottom-right {
                @include edgtfRelativeHolderLayout();
            }
	
	        @media only screen and (min-width: $ipad-portrait-plus-pixel) {
		
		        .edgtf-post-info-bottom-left,
		        .edgtf-post-info-bottom-right {
			        width: 50%;
		        }
		
		        .edgtf-post-info-bottom-left {
			        float: left;
		        }
		
		        .edgtf-post-info-bottom-right {
			        text-align: right;
			        float: right;
		        }
	        }
    
            .edgtf-post-info-bottom-right {
        
                > div {
                    margin-left: 17px;
            
                    &:first-child {
                        margin-left: 0;
                    }
                }
            }
        }
    
        //Link post style
        &.format-link {
            padding: 10px !important;
            background-image: url(../../../../../assets/img/default_pattern.png);

            .edgtf-post-text{
                padding: 26px 43px;
                background-color: #fff;
            }
        
            .edgtf-post-text-main {
                display: table;
                table-layout: fixed;
                height: 100%;
                width: 100%;
                text-align: left;
            }

            .edgtf-link-title{
                margin: 17px 0 6px;
            }
        
            .edgtf-post-mark {
                right: 20px;
                top: 22px;
                position: absolute;
            
                .edgtf-link-mark {
                    padding-top: 5px;
                    font-size: 36px;
                    color: $first-main-color;
                }
            }
        
            .edgtf-post-link-holder {
                @include edgtfTableCellLayout();
                vertical-align: top;
                text-align: left;
            }
        }
    
        //Quote post style
        &.format-quote {
            padding: 10px !important;
            background-image: url(../../../../../assets/img/default_pattern.png);
        
            .edgtf-post-text {
                padding: 26px 43px 26px;
                background-color: #fff;
            }
        
            .edgtf-post-text-main {
                display: table;
                table-layout: fixed;
                height: 100%;
                width: 100%;
                text-align: left;
            }

            .edgtf-quote-title{
                margin: 17px 0 6px;
            }

            .edgtf-quote-author{
                margin-top: 19px;
                display: block;
            }
        
            .edgtf-post-mark {
                @include edgtfTableCellLayout();
                width: 50px;
                vertical-align: top;
                text-align: left;
            
                .edgtf-quote-mark {
                    padding-top: 5px;
                    font-size: 36px;
                    color: $first-main-color;
                }
            }
        
            .edgtf-post-link-holder {
                @include edgtfTableCellLayout();
                vertical-align: top;
                text-align: left;
            }
        
            .edgtf-post-author {
                display: inline-block;
                margin-bottom: 23px;
                font-size: 13px;
                line-height: 20px;
            }
        }

        //Gallery post style
        &.format-gallery {

            .edgtf-post-image {
                width: 100%;
            }

            .owl-carousel .owl-item {
                z-index: 2;

                &.active {
                    z-index: 3;
                }
            }

            .edgtf-owl-slider .owl-nav {
                .owl-prev {
                    left: -15px;
                }
                .owl-next {
                    right: -15px;
                }
            }
        }

        //Audio post style
        &.format-audio {
    
            &.has-post-thumbnail {
        
                .edgtf-post-heading {
                    position: relative;
                }
        
                .edgtf-blog-audio-holder {
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    display: inline-block;
                    vertical-align: middle;
                }
            }
    
            .edgtf-post-image {
                width: 100%;
                max-width: inherit;
        
                img {
                    width: 100%;
                }
            }

            .edgtf-blog-audio-holder {

                .mejs-container, .mejs-embed, .mejs-embed body, .mejs-container .mejs-controls {
                    background: none;
                }

                .mejs-container {
                    background-color: #f3f3f3;
                }

                .mejs-container,
                .mejs-container .mejs-controls {
                    height: 32px !important;
                }

                .mejs-container .mejs-controls {
                    padding: 0 10px 0 4px;
                    box-sizing: border-box;
                }

                .mejs-container .mejs-controls > .mejs-playpause-button {
                    width: 39px;
                }

                .mejs-container .mejs-controls > .mejs-volume-button {
                    width: 30px;
                }

                .mejs-container .mejs-controls > .mejs-playpause-button,
                .mejs-container .mejs-controls > .mejs-volume-button {
                    height: 32px;

                    button {
                        position: relative;
                        height: 100%;
                        background: none;
                        background-position: center;
                        background-repeat: no-repeat;
                        margin: 0;
                        border: 0;
                        outline: none;
                    }

                    &.mejs-play button {
                        width: 39px;
                        background-image: url('../img/audio/play.png');
                    }

                    &.mejs-pause button {
                        width: 39px;
                        background-image: url('../img/audio/pause.png');
                    }

                    &.mejs-stop button {
                        width: 39px;
                        background-image: url('../img/audio/stop.png');
                    }

                    &.mejs-mute button {
                        width: 30px;
                        background-image: url('../img/audio/sound.png');
                    }

                    &.mejs-unmute button {
                        width: 30px;
                        background-image: url('../img/audio/mute.png');
                    }
                }

                .mejs-container .mejs-controls > .mejs-time {
                    height: 100%;
                    padding: 0;
                    font-family: inherit;
                    font-size: 13px;
                    color: rgba(23,23,23,.7);
                    line-height: 36px;
                }

                .mejs-container .mejs-controls > .mejs-time-rail {
                    height: 100%;
                    padding: 0;
                    font-family: inherit;

                    .mejs-time-total {
                        height: 5px;
                        margin: 15px 14px;
                        background: none;
                        background-color: #e8e8e8;

                        .mejs-time-current {
                            height: 5px;
                            background-color: $first-main-color;
                        }

                        .mejs-time-float {
                            top: -14px;
                            border: 0;
                            color: #fff;

                            .mejs-time-float-corner {
                                border-top-color: $default-heading-color;
                            }
                        }

                        .mejs-time-loaded,
                        .mejs-time-float {
                            background: none;
                        }
                    }
                }

                .mejs-container .mejs-controls > a.mejs-horizontal-volume-slider {
                    height: 32px;

                    .mejs-horizontal-volume-total,
                    .mejs-horizontal-volume-current {
                        height: 5px;
                        top: 15px;
                        background: none;
                    }

                    .mejs-horizontal-volume-total {
                        background-color: #e8e8e8;
                    }

                    .mejs-horizontal-volume-current {
                        background-color: $first-main-color;
                    }
                }
            }
        }

        &.format-video {
            
            .edgtf-self-hosted-video {
                max-width: 100%;
            }
        }
    }
}

/* ==========================================================================
   Blog Common style - end
   ========================================================================== */